const AnyReactComponent = ({ text }) => <img src={logoLumi}></img>;
const InfoWindow = (props) => {
    const { place } = props;
    console.log('place window', place);
    const infoWindowStyle = {
        position: 'relative',
        bottom: 150,
        left: '-45px',
        width: 220,
        backgroundColor: 'white',
        boxShadow: '0 2px 7px 1px rgba(0, 0, 0, 0.3)',
        padding: 10,
        fontSize: 14,
        zIndex: 100,
    };

    return (
        <div style={infoWindowStyle}>
            <div style={{ fontSize: 16 }}>
                {place.home}
            </div>
            <div style={{ fontSize: 14 }}>
                <span style={{ color: 'grey' }}>
                    {place.owner_account}
                    {' '}
                </span>
            </div>
            <div style={{ fontSize: 14, color: place.is_active == 1 ? base_color : inactive_color }}>
                {place.is_active == 1 ? 'Active' : 'Inactive'}
            </div>
            <div style={{ fontSize: 14, color: place.is_connect == 1 ? base_color : inactive_color }}>
                {place.is_connect == 1 ? 'Online' : 'Offline'}
            </div>
        </div>
    );
};
const Marker = ({ show, place, isConnect }) => {
    const markerStyle = {
        border: '1px solid white',
        borderRadius: '50%',
        height: 12,
        width: 12,
        backgroundColor: isConnect ? base_color : inactive_color,
        cursor: 'pointer',
        zIndex: 10,
    };

    return (
        <>
            <div style={markerStyle} />
            {/* {show && <InfoWindow place={place} />} */}
        </>
    );
};
const Map = function (props) {
    const [marker, setMarker] = useState([]);
    const history = useHistory();

    const defaultProps = {
        center: {
            lat: 20.9763271,
            lng: 105.791341
        },
        zoom: 8
    };
    let config = {
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem('access_token')
        }
    }
    const doGetListHc = async () => {
        const url = BASE_URL + '/hc-monitoring/home-coordinate';

        try {
            const res = await axios.get(url, config);
            console.log('resHC=========his', [...new Set(res.data.data)]);
            if (res.data.success) {
                setMarker([...new Set(res.data.data)].filter((item) => item.lat && item.long).map((item) => {
                    return {
                        ...item,
                        show: false

                    }
                }))
                // console.log('a');
                // let final = []
                // final.push({ name: 'Online', value: res.data.data.online })
                // final.push({ name: 'Offline', value: res.data.data.offline })
                // setDataChart(final)
                // console.log(final);
            }
            else if (res.data.statusCode === 500) {
                // renewToken();
                // doGetListHc();
                localStorage.removeItem('access_token');
                return history.push({ pathname: `/login` });

            }
            else{
                localStorage.removeItem('access_token');
                return history.push({ pathname: `/login` });
            } 


        } catch (err) {
            localStorage.removeItem('access_token');
            return history.push({ pathname: `/login` });
        }
    }
    useEffect(() => {
        let mounted = true;
        console.log('access_token', localStorage.getItem('access_token'));
        doGetListHc()
    }, [])
    const isFirstRender = useRef(true);
    console.log('marker', marker);
    const onChildClickCallback = (key) => {
        let newArr = [...marker]
        console.log('newArr', newArr);
        console.log('key', key);
        const index = marker.findIndex((e) => e.id == key)
        console.log('index', index);
        newArr[index].show = !newArr[index].show
        setMarker(newArr)
        console.log('key', key);
        // setMarker( => {
        //   const index = marker.findIndex((e) => e.id === key);
        //   marker[index].show = !marker[index].show; // eslint-disable-line no-param-reassign
        //   return marker;
        // });
    };
    console.log('marker length', marker.length);
    return (
        // Important! Always set the container height explicitly
        <div style={{ height: '100vh', width: '100%' }}>
            {!isEmpty(marker) && (
                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyAD-f29ebrulB0A1V1mmGcUZQ9ulUWoFuM" }}
                    defaultCenter={defaultProps.center}
                    defaultZoom={defaultProps.zoom}
                    yesIWantToUseGoogleMapApiInternals
                    // onChildClick={onChildClickCallback}

                >
                    {marker.map((place) => (
                        <Marker
                            key={place.id}
                            lat={place.lat}
                            lng={place.long}
                            show={place.show}
                            place={place}
                            isConnect={place.is_connect}
                        />
                    ))}
                </GoogleMapReact>
            )}
        </div>
    );
}
export default Map;
